import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
login : String;
password : String;

  constructor(
   private validateService : ValidateService, 
   private flashMessagesService : FlashMessagesService,
   private authService: AuthService,
   private router :Router) { }

  ngOnInit() {
  }



  onLoginSubmit(){
  	const user = {

		  login : this.login,
		  password : this.password
		  }


		  
		  
	// login user


	// suscribe user
  this.authService.authenticateUser(user).subscribe(data =>{  
  if(data.success){
   this.authService.StoreUserData(data.user);
    this.flashMessagesService.show('vous etes maintenant authentifie',{cssClass:'alert-success',timeout:4000});
    this.router.navigate(['/profile']);
   console.log(data);

  } else{
    this.flashMessagesService.show(data.msg,{cssClass:'alert-danger',timeout:4000});
      this.router.navigate(['login']);

  }
  });




  }
}



